.home-message {
    text-align: center;
    margin-top: -100px;
    padding: 40px;
    background-color: #f8beb37b;
  }
  
  .home-buy-btn {
    background-color: #a97c50;
    color: white;
    padding: 10px 30px;
    border: none;
    border-radius: 3px;
    font-weight: bold;
    cursor: pointer;
    transition: background 0.3s;
  }
  
  .home-buy-btn:hover {
    background-color: #8c643f;
  }
  