.promotion-banner {
  position: relative;
  width: 100%;
  height: 100vh;
  overflow: hidden;
}

.promotion-banner-image {
  width: 100%;
  height: 100%;
  object-fit: cover;
  transition: opacity 0.5s ease-in-out;
  position: absolute;
  top: 0;
  left: 0;
}

.banner-content {
  position: absolute;
  bottom: 50px;
  width: 100%;
  text-align: center;
  color: #333;
  z-index: 2;
  background: linear-gradient(to top, rgba(255, 228, 225, 0.8), transparent);
  padding-top: 30px;
}

.banner-content h2 {
  font-size: 24px;
  font-weight: bold;
  margin-bottom: 10px;
}

.banner-button {
  background-color: #7a5c3e;
  color: white;
  padding: 8px 20px;
  border-radius: 4px;
  font-size: 14px;
  text-decoration: none;
}

.banner-button:hover {
  background-color: #5a3f2c;
}
